export default {
	data() {
		return {
			checks: null
		}
	},
	onShow: function() {
		uni.onSocketMessage((res) => {
			this.getMessage(JSON.parse(res.data))
		})
		getApp().sendSocket("getChecks")
	},
	methods: {
		getMessage: function(data) {
			uni.hideLoading()
			let msg = data.msg
			let obj = data.obj
			if (msg == "loginError") {
				getApp().delCode()
				uni.redirectTo({
					url: "/pages/mine/mine"
				})
			} else if (msg == "checks") {
				this.checks = obj
			}
		},
		intoCheck: function (e) {
			let cid = e.currentTarget.dataset.cid
			uni.navigateTo({
				url: "/pages/check/checkList?id=" + cid
			})
		},
		barTo: function(e) {
			let name = e.currentTarget.dataset.name
			if (name == "scan") {
				getApp().scan()
			} else {
				getApp().barTo(name)
			}
		}
	}
}
